i{
    color: white;
}
i:hover{
    cursor: pointer;
}
i>.quantity{
    display: inline-block;
    width: 18px;
    height: 18px;
    color: #0c0b10;
    font-weight: 700;
    font-size: .7em;
    text-align: center;
    line-height: 18px;
    border-radius: 50%;
    background-color: #eabf00;
    position: absolute;
    bottom: -0.25px;
    right: 0;
}
.card{
    border: none!important;
}
.card:hover{
    border: 1px solid rgba(0,0,0,.125)!important;
    cursor: pointer;
}
.card:hover>.card-body>.btn{
    color: white;
    background-color:black;
}
.card>.card-body>.fs-6::before{
    content: "";
    width: 20px;
    height: 2px;
    background-color: #eabf00;
    position: absolute;
    bottom:-10px;
    left: 50%;
    margin-left: -10px;
}
.form-select{
    width: 13.75%!important;
    margin-left:16.75rem;
}
.btn{
    border-radius: 0%!important;
}
