.i{
    color: white;
}
.navbar{
    width: 100%;
}
.nav-item:hover{
    cursor: pointer;
}
.nav-item>.quantity{
    display: inline-block;
    width: 18px;
    height: 18px;
    color: #0c0b10;
    font-weight: 700;
    font-size: 1.75em;
    font-family: sans-serif;
    text-align: center;
    line-height: 18px;
    border-radius: 50%;
    background-color: #eabf00;
    position: absolute;
    bottom: -0.25px;
    right: 0;
}
